* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
  scroll-behavior: smooth;
}
@import url("https://fonts.googleapis.com/css2?family=Courgette&family=Lemonada&family=Open+Sans&family=Pacifico&display=swap");

:root {
  --fontLemonada: "Lemonada";
  --fontCourgette: "Courgette";
  --fontOpenSans: "Open Sans";
  --fontPacifico: "Pacifico";
  --main_Back_color: #f8fbfe;
  --primary: #000;
  --secondary: #51c9ff;
}
*::-webkit-scrollbar {
  display: none;
}

* {
  -ms-overflow-style: none;
  scrollbar-width: none; /* Firefox */
}
html,
body,
#root,
.main {
  width: 100%;
  height: 100%;
}
.main {
  overflow: scroll;
  background-color: var(--main_Back_color);
  max-width: 1600px;
  margin: auto;
}
#root {
  background-color: var(--main_Back_color);
}
nav {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 35px;
}
nav h2 {
  font-family: var(--fontCourgette);
  color: var(--primary);
}
nav li {
  display: inline-block;
  margin: 0 15px;
  font-size: 17px;
  font-weight: bold;
}
nav a {
  text-decoration: none;
  color: var(--primary);
  font-family: var(--fontOpenSans);
}
.mobileMenu {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  color: var(--primary);
  display: none;
  z-index: 99;
  background-color: var(--main_Back_color);
  animation-name: show;
  animation-fill-mode: forwards;
  animation-duration: 0.5s;
}
.burgerButton {
  display: none !important;
}
.closeButton {
  color: var(--primary) !important;
  font-size: 30px !important;
}
.iconButton {
  color: var(--primary) !important;
  position: fixed !important;
  top: 15px !important;
  right: 15px !important;
}

.line1 {
  width: 27px;
  height: 3px;
  border-radius: 50px;
  background-color: var(--primary);
  margin: 5px 0;
}
.for-margin {
  margin: 10px 0;
}
.show {
  display: block !important;
}
.firstSection {
  width: 100%;
  margin: 0px auto;
  color: var(--main_Back_color);
  padding: 80px 50px 80px 50px;
  background-color: #000;
}

.imgInfo {
  display: flex;
  justify-content: center;
}
.textInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.textInfo p {
  font-family: var(--fontOpenSans);
  color: var(--main_Back_color);
  font-size: 18px;
  letter-spacing: 1px;
}
.textInfo h1 {
  text-align: left;
}
.cimg {
  border-radius: 10%;
  width: 90%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.circle-img {
  background-color: transparent;
  border: 1px solid var(--primary);
}
.circle-img2 {
  background-color: transparent;
  /* border-left: 1px solid var(--primary);
  border-right: 1px solid var(--primary); */
}
.circle-img3 {
  background-color: transparent;
  background-color: var(--main_Back_color);
}
.firstSection h1 {
  font-family: var(--fontLemonada);
  font-size: 40px;
}
.firstSection p {
  padding: 5px 0;
}
.flex-btn {
  margin-top: 30px;
}
.btn {
  background-color: var(--primary) !important;
  color: var(--main_Back_color) !important;
  font-family: var(--fontLemonada) !important;
  padding: 8px 15px !important;
}
.firstSection .btn {
  box-shadow: inset -1px -1px 5px #ecf0f1;
}
.flex-btn .btn:nth-child(2) {
  margin-left: 25px !important;
}
.secondSection {
  margin-top: 50px;
  padding: 0 50px;
  display: grid;
  grid-template-columns: 0.5fr 0.5fr;
}
.secondSection h1 {
  font-family: var(--fontLemonada);
  margin-bottom: 30px;
  color: var(--primary);
}
.techSection {
  margin-top: 40px;
  padding: 0 50px;
}
.techSection h4 {
  font-family: var(--fontLemonada);
  border: 2px solid var(--primary);
  border-radius: 5px;
  display: inline-block;
  padding: 0 5px;
  color: var(--primary);
  font-weight: 400;
}
.stepper {
  width: 90%;
  margin: auto;
  color: var(--primary);
}
.primary-color {
  background-color: var(--primary) !important;
  font-family: var(--fontOpenSans) !important;
}
.css-1u4zpwo-MuiSvgIcon-root-MuiStepIcon-root.Mui-completed {
  color: var(--primary) !important;
}
.css-1u4zpwo-MuiSvgIcon-root-MuiStepIcon-root.Mui-active {
  color: var(--primary) !important;
}
.stepper p {
  font-family: var(--fontOpenSans) !important;
  margin: 10px 0 !important;
}
.info-p {
  margin: 20px 0;
  font-family: var(--fontLemonada);
  border: 2px solid var(--primary);
  display: inline-block;
  padding: 0 5px;
  color: var(--primary);
  border-radius: 5px;
}
