} */
/*  mobile me dia */
.centerSlide {
  margin: 50px auto !important;
  width: 80% !important;
  max-width: 1300px;
  margin: 50px auto !important;
}
.centerSlide div {
  height: 150px;
  text-align: center;
  transition: 1s;
}
.centerSlide div img {
  width: 80px;
  height: 80px;
  margin: auto;
}
.margin {
  margin-top: 50px;
}
.slick-next:before,
.slick-prev:before {
  color: var(--primary);
  opacity: 1 !important;
}
.slick-disabled {
  opacity: 0.5 !important;
}
.Mui-disabled {
  opacity: 0.5 !important;
}
.inside {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.inside p {
  font-family: var(--fontOpenSans);
}
.appIco {
  position: fixed;
  bottom: 20px;
  right: 20px;
  cursor: pointer;
}
.contact {
  padding: 50px;
}
.forflex {
  width: 100%;
  display: flex;
}
.contact h1 {
  font-family: var(--fontLemonada);
  padding-bottom: 50px;
}

.inpText {
  width: 46% !important;
  margin: 10px !important;
  height: 45px;
  background-color: transparent;
  border: 1px dashed var(--primary);
  color: var(--primary);
  padding: 0 10px;
  font-size: 17px;
  border-radius: 5px;
}
.textarea {
  width: 95%;
  display: block !important;
  height: 250px;
  margin: 10px;
  overflow-x: hidden;
  padding: 10px;
  font-family: var(--fontOpenSans);
  font-size: 17px;
  border: 1px dashed var(--primary);
  border-radius: 5px;
  background: transparent;
  color: var(--primary);
}
.firstContact {
  width: 50%;
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* ----------------------------------------- Accordion ------------------------------------------------------- */

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  position: fixed;
  justify-content: center;
  background-color: var(--primary);
  z-index: 101;
  top: 0;
  left: 0;
  color: var(--main_Back_color);
  transition: 1s;
}

.Circular {
  margin-right: 20px;
}
.hideLoad {
  transform: scale(2);
  opacity: 0;
}
.flex {
  display: inline-flex;
}
.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-column {
  flex-direction: column;
}
.wrapper {
  width: 100%;
  color: var(--main_Back_color);
  font-family: var(--font2);
  padding: 5px 50px 25px 50px;
  max-width: 1600px;
  margin: auto;
  background-color: var(--main_Back_color);
}
.resume-btn {
  margin-top: 20px !important;
  font-family: var(--font2) !important;
  width: 120px !important;
  font-size: 17px !important;
}
.works {
  margin-top: 20px;
}
.works-info {
  font-family: var(--fontLemonada);
  border: 2px solid var(--primary);
  display: inline-block;
  padding: 0 5px;
  color: var(--primary);
  border-radius: 5px;
}
.items {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr;
  margin-top: 50px;
  grid-row-gap: 80px;
  grid-column-gap: 20px;
}
.box {
  padding: 10px;
  border-radius: 10px;
  color: var(--main_Back_color);
  position: relative;
}

.MuiSvgIcon-root {
  color: var(--primary) !important;
  font-size: 30px !important;
}
.MuiCircularProgress-svg {
  color: var(--main_Back_color) !important;
}
.css-i9fmh8-MuiBackdrop-root-MuiModal-backdrop {
  background: rgba(0, 0, 0, 0.9) !important;
}
.box {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-radius: 1px solid #fff !important;
}
.box img {
  width: 100%;
  object-fit: contain;
}

