  padding: 0 5px;
  color: var(--primary);
  border-radius: 5px;
  font-weight: 400;
  margin-bottom: 50px;
}
.contact textarea {
  resize: vertical;
}
.contact-info {
  width: 50%;
}
.contact-info p {
  width: 80%;
