@media (max-width: 1227px) {
  .info h2 {
    font-size: 20px;
  }
  .tab {
    font-size: 15px !important;
  }
  .tab-btn {
    margin: 5px 0 !important;
    font-size: 15px !important;
  }
  .acc {
    font-size: 16px !important;
    padding: 0 8px !important;
  }
}
@media (max-width: 1150px) {
  /* .forflex {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  } */
  .secondSection,
  .techSection {
    padding: 0 20px;
  }
  .firstSection {
    padding: 80px 20px 30px 20px;
  }
  .contact {
    padding: 40px 20px;
  }
  .inpText {
    width: 90% !important;
  }
  .textarea {
    width: 90% !important;
  }
  .burgerButton {
    display: block !important;
  }
  nav ul {
    display: none;
  }
  .mobileMenu ul {
    display: flex !important;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .mobileMenu li {
    margin: 10px 0;
  }
  .firstSection h1 {
    font-size: 35px;
  }
}

@media (max-width: 1050px) {
  .img-center {
    width: 100%;
    justify-content: flex-start;
  }
  .box {
    grid-template-columns: 1fr;
  }
  .info {
    margin-top: 20px;
  }
  .box img {
    width: 70%;
  }
  .firstSection h1,
  .secondSection h1 {
    font-size: 30px;
  }
}
@media (max-width: 700px) {
  .box img {
    width: 100%;
  }
  .firstSection p {
    font-size: 16px;
  }
  .firstSection h1,
  .secondSection h1 {
    font-size: 22px;
  }
  .secondSection {
    margin-top: 70px;
    grid-template-columns: 1fr;
  }
  .secondSection h1 {
    margin-bottom: 20px;
  }
  .forflex {
    flex-direction: column;
  }
  .textarea {
    margin: 10px 0 !important;
  }
  .inpText {
    margin: 10px 0 !important;
  }
  .firstContact {
    width: 95%;
    margin: auto;
  }
  .contact-info {
    width: 95%;
    margin: auto;
  }
  .contact-info p {
    width: 95%;
  }
}
@media (max-width: 580px) {
  .box img {
    width: 70%;
  }
  .items {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 430px) {
  .box img {
    width: 100%;
  }
  .wrapper {
    padding: 10px;
  }
}
